class ParticleSystem {
  constructor(
    private markedForDeletion = false,
    private size = 0,
    private x = 0,
    private y = 0,
    private speedX = 0,
    private speedY = 0,
  ) {}

  update() {
    this.move()
    this.destroyed()
  }
  move() {
    // 粒子的移动
    this.x -= this.speedX
    this.y -= this.speedY
  }
  destroyed() {
    // 粒子不断变小
    this.size *= 0.95
    if (this.size < 0.5) this.markedForDeletion = true //基于大小的清除
  }
}
